  src: url("/src/assets/fonts/Inter/Inter.var.woff2") format("woff2 supports variations"), url("/src/assets/fonts/Inter/Inter.var.woff2") format("woff2-variations");
  font-variation-settings: normal;
}
@font-face {
  font-family: "Inter var experimental";
  font-weight: 100 900;
  font-display: swap;
  font-style: oblique 0deg 10deg;
  src: url("/src/assets/fonts/Inter/Inter.var.woff2?v=3.19") format("woff2");
