body {
  margin: 0;
  background-color: #D5D7D4;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

a {
  color: #2b2b2b;
  transition: all linear 0.2s;
}

.link, .language-toggle-button, .footer-link {
  transition: all linear 0.2s;
  font-variation-settings: "wght" 300;
  letter-spacing: 0em;
}

.link, .footer-link > p {
  position: relative;
  text-decoration: none;
}
.link::before, .link::after, .footer-link > p::before, .footer-link > p::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 1px;
  bottom: 0;
  left: 0;
  background-color: black;
  transition-duration: 0.35s;
  transition-timing-function: ease;
  transition-property: background-position, transform;
  z-index: 1;
  box-sizing: border-box;
}
.link::before, .footer-link > p::before {
  opacity: 0.6;
}
.link::after, .footer-link > p::after {
  transform: scaleX(0);
  transform-origin: right center;
  animation: GradientScroll 2s linear infinite;
  background-size: 200% 100%;
  background-image: linear-gradient(140deg, #d97d01, #01928f 40%, #01928f 50%, #9e81d2 60%, #d97d01 80%, #d97d01);
}

@media (hover: hover) and (pointer: fine) {
  .link:hover, .language-toggle-button:hover, .footer-link:hover {
    letter-spacing: -0.02em;
    font-variation-settings: "wght" 500;
  }
  .link:hover::after, .footer-link:hover > p::after {
    transform: scaleX(1);
    transform-origin: left center;
  }
  a:hover:not(:where(.link, .footer-link)) {
    color: #93748A;
  }
}
.landing-page {
  height: 100vh;
  display: grid;
  grid-template-columns: minmax(100px, 1fr) 8fr minmax(100px, 1fr);
  position: relative;
  overflow-x: hidden;
}
.landing-page .landing__main-text-container {
  position: relative;
}
.landing-page .landing__main-text {
  font-variation-settings: "wght" 200;
  font-size: 4rem;
  line-height: 1.2;
  position: absolute;
  z-index: -1;
  bottom: 0;
  left: -300px;
}
.landing-page .landing__main-text > p {
  white-space: nowrap;
}
.landing-page .landing__main-text__heading {
  position: relative;
  display: inline-block;
  padding: 0 8px;
  margin: 0 8px;
}
.landing-page .landing__main-text__heading:before {
  content: attr(data-text);
  display: block;
  position: absolute;
  box-sizing: border-box;
  top: 0;
  padding: 0;
  left: 0;
  width: 0;
  overflow: hidden;
  color: #D5D7D4;
  background-color: #2b2b2b;
  animation: HeadingBackground cubic-bezier(0.22, 0.61, 0.36, 1) 0.8s 0.4s both;
}
.landing-page .landing__right-links, .landing-page .landing__left-links {
  font-size: 0.875rem;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  background-color: #fafafa;
  position: absolute;
  width: 100%;
  height: 100%;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.3);
  z-index: 1;
}
.landing-page .landing__left-links {
  animation: LeftBarSlide cubic-bezier(0.22, 0.61, 0.36, 1) 0.8s;
  align-items: center;
}
.landing-page .landing__right-links {
  animation: RightBarSlide cubic-bezier(0.22, 0.61, 0.36, 1) 0.8s;
  align-items: center;
}
.landing-page .language-toggle-button {
  border: none;
  background-color: transparent;
  cursor: pointer;
  font-size: 0.875rem;
  padding: 0;
  transition: all linear 50ms;
  color: rgb(122, 122, 122);
}
.landing-page .language-toggle-button.chosen {
  font-weight: 500;
  color: black !important;
}
.landing-page .language-toggle-button #active-lang {
  color: black;
}
.landing-page .language-toggle-button:first-child {
  margin-right: 16px;
}
.landing-page .landing__link-outer.top {
  margin-top: 80px;
}
.landing-page .landing__link-outer.bottom {
  margin-bottom: 80px;
}
.landing-page .landing__left-links__outer, .landing-page .landing__right-links__outer {
  position: relative;
}

.container {
  overflow: hidden;
}

.scrolling-text {
  display: inline-block;
  transition: transform 0.5s cubic-bezier(0.23, 0.36, 0.28, 0.83);
  will-change: transform;
  backface-visibility: hidden;
}

.scrolling-text .scrolling-text-content {
