@media (hover: hover) and (pointer: fine) {
  .works__card:hover .works__card__footer {
    border-color: #D5D7D4;
  }
  .glow:hover:hover:before {
    opacity: 1;
    left: -3px;
    top: -3px;
    width: calc(100% + 6px);
    height: calc(100% + 6px);
  }
