.works {
  background-color: #2b2b2b;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-auto-rows: calc((100vw - 280px - 880px) / 12);
  gap: 80px;
  grid-auto-flow: dense;
  padding: 120px 140px;
}
.works__card {
  color: #D5D7D4;
  display: grid;
  grid-template-rows: auto 1fr auto;
}
.works__card.vertical {
  grid-column: span 4;
  grid-row: span 7;
}
.works__card.horizontal {
  grid-column: span 8;
  grid-row: span 5;
}
.works__card.big {
  grid-column: span 12;
  grid-row: span 7;
}
.works__card-name {
  font-size: 1.5rem;
}
.works__card-date {
  font-size: 0.875rem;
}
.works__card__gif-cover {
  background-color: #D5D7D4;
  margin-bottom: 1.25rem;
  position: relative;
  cursor: pointer;
  min-height: 140px;
}
.works__card__gif-cover .works__card__noise {
  width: 100%;
  height: 100%;
  position: absolute;
  border-radius: 8px;
  transition: all 0.3s ease-out;
  opacity: 0.5;
  z-index: 2;
}
.works__card__gif-cover video {
  transition: filter 0.3s ease-out;
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 8px;
  filter: grayscale(0.8);
}
.works__card__top {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 0.625rem;
  position: relative;
}
.works__card__footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  text-decoration: none;
  padding: 10px 0;
  font-size: 0.875rem;
  border-top: 1px dotted #808080;
  border-bottom: 1px dotted #808080;
  transition: 0.5s;
  transition-property: all;
  transition-duration: 0.5s;
  transition-timing-function: ease;
  transition-delay: 0s;
}
.works__card__footer__arrow {
  width: 30px;
  height: 30px;
  margin-left: 16px;
}

.glow {
  position: relative;
  z-index: 1;
  transition: all 0.3s ease-out;
  border-radius: 10px;
}
.glow:before {
  content: "";
  background: linear-gradient(140deg, #d97d01, #01928f 40%, #01928f 50%, #9e81d2 60%, #d97d01 80%, #d97d01);
  position: absolute;
  top: 0;
  left: 0;
  background-size: 400%;
  z-index: -1;
  animation: ImageGradient 4s linear infinite alternate;
  opacity: 0;
  transition: all 0.3s ease-out;
  border-radius: 10px;
  transform-origin: center;
  transform: translateZ(0);
  width: 100%;
  height: 100%;
}
.glow:after {
  z-index: -1;
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background: #e5e5e5;
  left: 0;
  top: 0;
  border-radius: 8px;
}
.glow:active::after {
  background: rgb(16, 16, 16);
}

