.tools {
  background-color: #2b2b2b;
  color: white;
}

.tools__heading {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}
.tools__heading-line {
  width: max-content;
  flex: 1;
  height: 1px;
  background-color: white;
}
.tools__heading-text {
  font-size: 0.875rem;
  padding: 0 46px;
}

.tools__text {
  font-weight: 200;
  font-size: 3rem;
  overflow: hidden;
  padding-bottom: 20px;
}
.tools__text .wrapper {
  max-width: 100%;
  overflow: hidden;
}
.tools__text .marquee {
  white-space: nowrap;
