  display: inline-block;
  animation: marquee 25s linear infinite;
}
.tools__text .marquee p {
  display: inline-block;
}
@keyframes marquee {
  0% {
    transform: translate3d(0, 0, 0);
  }
  100% {
    transform: translate3d(-50%, 0, 0);
  }
}

